<template>
    <section>
        <tg-list v-show="IsFormBuilderView" :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="FormBuilder"
            :LegendArray="[]" :widgets="[]" IdentityColumn="Id" :HeaderText="$t('FormBuilder')" :listType="['List']"
            :ListData="TicketSourceList" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchRecords"
            v-on:HeaderButtonClick="commonHeaderButtonClick" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :RenderRowActionMethod="renderActions" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:ActionButtonClick="FormBuilderactionButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'CreatedDate'">
                    <span>
                        {{ $options.filters.formatDate(data.row.CreatedDate) }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'FormType'">
                    <span v-if="data.row.FormType == 1">{{ $t('FORM') }}</span>
                </template>
            </template>
        </tg-list>
        <AddForm v-if="showFormBuilderPopup" @Close="Close"></AddForm>
        <SetFormMapping v-if="SetFormMapping" :Form_Id="Form_Id" :IsCheckLocate="IsCheckLocate" :Form="moduleName"></SetFormMapping>
    </section>
</template>
<script>
import Schema from "@/views/FormBuilder/FormBuilderSchema";
import DataServices from "@/services/DataService";
import AddForm from "./AddForm.vue";
import SetFormMapping from '../../components/Modal/SetFormMappingModal.vue'
export default {
    components: {
        AddForm, SetFormMapping
    },
    data() {
        return {
            SetFormMapping: false,
            showFormBuilderPopup: false,
            showSetupTickectSource: false,
            showExpanded: '',
            StatusMappingList: [],
            noRecords: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            TotalPages: 0,
            SearchCondition: '',
            TicketSourceList: [],
            GroupStart: 1,
            GroupEnd: 3,
            user_id: null,
            companyId: null,
            ticketSourceId: 0,
            IsFormBuilderView: true,
            moduleName:'FormBulider',
            Headers: [{
                "COLUMN_NAME": "FormName",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Form Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "FormType",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Form Type",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "Description",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Description",
                "DISPLAY_ORDER ": 3,
            },
            {

                "COLUMN_NAME": "CreatedBy",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created By",
                "DISPLAY_ORDER": 4,

            },
            {

                "COLUMN_NAME": "CreatedDate",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created At",
                "DISPLAY_ORDER": 5,

            }
            ],
            Form_Id: '',
            IsCheckLocate: ''
        }

    },
    created: function () {  
        var vm = this;
        vm.user_id = this.GetUserInfo.ID
        vm.user_type = this.GetUserInfo.UserType
        vm.companyId = this.GetUserInfo.CompanyId;
        vm.FetchRecords();
    },
    methods: {
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        leftCommonSearch: function (value) {
            var vm =this;
            const searchParams = new URLSearchParams(value.searchConditionJson);
            const entries = searchParams.entries();
            const params = this.paramsToObject(entries);
            if (params.hasOwnProperty('FormName')) {
                vm.SearchCondition = params['FormName'];
            } else {
                vm.SearchCondition = ''
            }
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            this.FetchRecords();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.DeleteFormBuilderListRecord();
                    break;
                case "openAddFormPopup":
                    this.openAddFormPopup();
                    break;
            }
        },
        FormBuilderactionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'Edit':
                    this.EditFormBuilder(item);
                    break;
                case 'MappingBuilder':
                    this.MappingBuilder(item);
                    break;
                case 'CommonDelete':
                    this.DeleteFormBuilderListRecord(item.Id);
                    break;
                case 'ClickOnView':
                    this.ClickOnView(item);
                    break;
            }
        },
        ClickOnView:function(item){
            var vm=this;
            vm.$router.push({path: '/FormBuilder/View/'+item.Id+''});
        },
        EditFormBuilder: function (item) {
            var vm = this;
            vm.$router.push({ name: 'ManageFormBuilder', params: { FormId: item.Id, ModuleId: item.ModuleId, SubModuleId: item.SubModuleId, FormName: item.FormName, FormType: item.FormType, Desc: item.Description } });
        },
        MappingBuilder: function (item) {
            this.SetFormMapping = true;
            this.Form_Id = item.Id;
            this.IsCheckLocate = item.IS_LOCATE_OR_NONLOCATE;
        },
        openAddFormPopup: function () {
            var vm = this;
            vm.showFormBuilderPopup = true;
        },
        Close: function () {
            var vm = this;
            vm.showFormBuilderPopup = false;
        },
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },
        formatDateTime: function (value) {
            if (value) {
                return moment(value).format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(Globalize.culture().calendar.patterns.t).replace("t", "A").replace("t", ""))
            }
        },
        onEnter: function (e) {
            var vm = this;
            if (e.charCode == 13) {
                e.preventDefault();
                vm.SearchCondition = vm.SearchCondition;
                    vm.FetchRecords();
            }
        },       
        refreshList: function () {
            this.SearchCondition = "";
            this.FetchRecords();
        },
        DeleteSetupTickectSourceList: function (event) {             
            var vm = this;
            var val = "";
            vm.CurrentPage = 1;
            vm.PageSize = 10;
            vm.PageNumber = 1;
            vm.FetchRecords();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Mapping':
                        action.isVisible = true;
                        break;
                    case 'View':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        Searchdata: function () {
            var vm = this;
            //do something
            vm.SearchCondition = vm.SearchCondition;
            vm.FetchRecords();
        },
        refreshList: function () {
            this.SearchCondition = "";
            this.FetchRecords();
        },
        DeleteFormBuilderListRecord: function (event) {
            var vm = this;
            var val = "";
            if ($('.chkItems:checkbox:checked').prop('checked') == true) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }else{
                val += event;
            }
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("FormBuilder")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FormBuilder",
                        subModuleCode: "FormBuilder",
                        refCode: "TALYGEN_Form",
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.DeleteMultipleRecords(objectPostString).then(response => {
                        var box = "";
                        if (response.data[0].Status == "Success") {                           
                            vm.FetchRecords();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('FormBuilder')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('FormBuilder')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        addEditTemplate: function (id) {
            var vueObj = this;
            var url = `TempleteId=${id}`;
            DataServices.GetTemplateById(url).then(function (response) {
                if (response.data != null && response.data != "") {                   
                    if (response.data.status == "Success") {
                        model.TempleteId = response.data.result.data[0].ID;
                       
                    }
                    else {
                    }
                    
                }
            });
        },

        FetchRecords: function () {            
            var vueObj = this;
            if (!vueObj.isBlank(vueObj.SearchCondition)) {
                vueObj.PageNumber = 1;
            }
            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}`;
            if (!vueObj.isBlank(vueObj.SearchCondition)) {
                url = url + "&search=" + vueObj.SearchCondition
            }
            DataServices.GetFormList(url).then(function (response) {
                vueObj.Headers.forEach((column) => {
                    column.settings = null;
                    if (column.COLUMN_NAME == 'CreatedDate') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'FormType') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                });
                vueObj.TicketSourceList = [];
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        vueObj.TicketSourceList = response.data.result.data;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.listheaderbuttons[1].isdisabled=true;
                        if (typeof(vueObj.TicketSourceList)!='undefined' && vueObj.TicketSourceList.length > 0) {
                            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                            vueObj.NoRecord = false;
                        }
                        else {
                            vueObj.NoRecord = true;
                        }
                    }
                    else {
                        vueObj.NoRecord = true;
                    }
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                }
            });
        },
        openPopupForSetupTickectSource: function (id) {
            this.ticketSourceId = id;
            this.showSetupTickectSource = !this.showSetupTickectSource;
        },
    }
}
</script>