<template>
     <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">Add Form</h4>
                        <button type="button" class="close" v-on:click="$emit('Close')">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form ref="AddForm" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    created:function(){
        var vm=this;
        if(vm.moduleId>0){
            vm.FormSchema[0].Data[0].value=vm.formName
            vm.FormSchema[0].Data[2].value=vm.moduleId;
            vm.FormSchema[0].Data[4].value=vm.description;
        }
        else{
            vm.FormSchema[0].Data[0].value=vm.formName;
            vm.FormSchema[0].Data[2].value=vm.moduleId;
            vm.FormSchema[0].Data[4].value=vm.description;
        }
        vm.GetModuleList();
    },
    data() {
      return{
        ModuleList:[],
        SelectedModuleId:0,
        SelectedSubModuleId:0,
        SubModuleList:[],
        FormSchema: [
                
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'Form Name',
                            name: "txtFormName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "RadioField", 
                            label: "Select Form Type",                          
                            name: "Type",
                            value: 1,
                            placeholder: "",
                            validationRules: { "required": false, max: 200 },           
                            config: {
                                options: [{name:'Form', value: 1 }],
                                showAddIcon: false,
                                onChange: this.onChangeRadio
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Module'),
                            name: "Module",
                            disabled:this.moduleId>0?true:false,
                            value: "",
                            config: {
                                options: [],
                                onChange: this.onChangeDropdown
                            },
                            placeholder: "Select Module",
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Sub Module'),
                            disabled:this.moduleId>0?true:false,
                            visibility:false,
                            name: "Priority",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.onChangeSubDropdown
                            },
                            placeholder: "Select Sub Module",
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "TextAreaField",
                            mode: "Description",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Description'),
                            validationRules: { "required": true,},
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-primary",
                    isVisible:this.moduleId>0?false:true,
                    text: "<i class='fa fa-forward pr-2'></i>Next",
                },
                {
                    type: "submit",
                    class: "btn btn-primary",
                    isVisible:this.moduleId>0?true:false,
                    text: "<i class='fa fa-forward pr-2'></i>Update",
                    onClick: this.Update
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
      }  
    },
    props:{
        formName:{
            required:false
        },
        description:{
            required:false
        },
        moduleId:{
            required:false
        },
        subModuleId:{
            required:false
        }
        
    },
    methods:{
        Close:function(){
            var vm=this;
            vm.$emit("Close");
        },
        onSubmit:function(){
            var vm=this;
            vm.$router.push({ name: 'ManageFormBuilder', params: {FormId:0, ModuleId: vm.SelectedModuleId, SubModuleId: vm.SelectedSubModuleId,FormName:vm.FormSchema[0].Data[0].value,FormType:1,Desc:vm.FormSchema[0].Data[4].value} });
        },
        Update:function(){
            var vm=this;
            var FormName=vm.FormSchema[0].Data[0].value;
            vm.$emit("EditFormName",FormName);
        },
        GetModuleList:function(){
            var vm=this;
            DataService.GetModuleList().then((response) => {
                response.data.DATA.forEach(element => {
                    vm.ModuleList.push({
                        name:element.name,
                        value:element.value
                    })
                });
                // debugger
                if(response.data.DATA.length>0){
                    vm.FormSchema[0].Data[2].config.options = response.data.DATA;
                    vm.$refs.AddForm.UpdateKeyValue();
                }
            })
        },
        onChangeDropdown:function(e){
            var vm=this;
            var id=e.target.value;
            vm.SelectedModuleId=id;
            vm.FormSchema[0].Data[3].visibility=true;
            vm.SubModuleList=[];
            var params=`moduleId=${id}`
            DataService.GetSubModuleListByModuleId(params).then((response) => {
                response.data.DATA.forEach(element => {
                    vm.SubModuleList.push({
                        name:element.SUB_MODULE_NAME,
                        value:element.SUB_MODULE_ID
                    })
                });
                vm.FormSchema[0].Data[3].config.options = vm.SubModuleList;
                vm.$refs.AddForm.UpdateKeyValue();
            })
        },
        onChangeSubDropdown:function(e){
            var vm = this;
            var id = e.target.value;
            vm.SelectedSubModuleId=id;
        }
    }
    
}
</script>
